@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@900");





.App {
  text-align: center;
  font-family: 'Nunito', sans-serif;
  color:#d1c413; 
  font-size: 1.5vw;
  -webkit-text-stroke: 1px #5b1708;
}

* {
  box-sizing: border-box;

}
 
body{
  overflow: hidden;
}

.overlay{
  
  width:100%;
  height:100;
  z-index: 10;
  top: 0;
  left: 0;
  position:static;
}

.moving-background{
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url("./assets/background/7OQF.gif");
  background-size:100% 100%;
  background-repeat:no-repeat;
  background-attachment:fixed;
  background-position: 40% 40%;
  
  

}
